$m-size;\n}\n\n","//widget\n.widget{\n    background-color: $light-blue;\n    margin-bottom: $xl-size;\n}\n\n//wigetleement\n.widget__message{\n    color:$off-white;\n    margin: 0;\n    padding: $l-size;\n    text-align: center;\n    border-bottom: .1rem solid lighten($light-blue,10%);\n}\n\n//widget header\n.widget-header{\n    background: $blue;\n    color: $off-white;\n    padding: $m-size;\n    display: flex;\n    justify-content: space-between;\n}\n\n.widget-header__title{\n    margin: 0;\n}",".option{\n    border-bottom: 1px solid lighten($light-blue,10%);\n    display: flex;\n    justify-content: space-between;\n    padding: $l-size $m-size;\n}\n\n.option__test{\n    color:white;\n    font-weight: 500;\n    font-size: 2rem;\n    margin: 0;\n    text-transform: capitalize;\n    word-break: break-all;\n} ","//addoptpionerror\n.addoptionerror{\n    color: $off-white;\n    font-style: italic;\n    margin: $m-size 0 0 0;\n    padding:0 $m-size;\n    text-transform: capitalize;\n}\n\n//form add option\n.add-option{\n    display: flex;\n    flex-direction: column;\n    padding: $m-size;\n\n}\n\n.add-option__input{\n    flex-grow: 1;\n    background: $dark-blue;\n    border: none;\n    border-bottom: .3rem solid darken($dark-blue,10%);\n    margin: 0 0 $s-size 0;\n    color: $off-white;\n    padding: $s-size;\n}\n\n@media (min-width:$desktop-breakpoint) {\n    .add-option{\n        flex-direction: row;\n    }\n    .add-option__input{\n        margin:0 $s-size 0 0;\n    }\n\n}",".ReactModalPortal > div {\n    opacity: 0;\n}\n.ReactModalPortal .ReactModal__overlay{\n    align-items: center;\n    display: flex;\n    justify-content: center;\n    transition: opacity 200ms ease-in-out;\n}\n\n.ReactModalPortal .ReactModal__overlay--after-open{\n    opacity: 1;\n}\n\n.ReactModalPortal .ReactModal__overlay--before-close{\n    opacity: 0;\n}\n\n.modal{\n    background: $light-blue;\n    color: white;\n    max-width: 30rem;\n    outline: none;\n    padding: $l-size;\n    text-align: center;\n\n}\n\n.modal__title{\n    margin: 0 0 $m-size 0;\n}\n.modal__body{\n    font-size: 2rem;\n    font-weight: 300;\n    text-transform: capitalize;\n    margin: 0 0 $l-size 0;\n    word-break: break-all;\n}\n"],"sourceRoot":""}]);
